.grid-flex{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    gap: 1.5rem;
    margin: 1.5rem 2rem;
}
.list-table{
    width: 80%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

@media (max-width:1200px){
    .list-table{
        width: 95%;
    }
}